n. During the process of planning and running events, an over-division of hierarchical levels can reduce the activeness and creativity of new members. We are a group of people working together, not a single person. ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 3 ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 3Constantly improve the organization of the group. As the group grows, build clear organizational structure and establish clearly the responsibility of each member. This increases the groups efficiency. ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 4// ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 4Team building and group training is very important. Host teaming-bonding events and trainings on sex/gender-related topics regularly. Through this, members can receive better support, the group can become more unified, more sensitive to related topics on sex/gender, and more prone to action. ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 5LGBT ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 5Constantly expand sources of funding. Fundraise through in-person activities or online advertising, charity sales, and small grants open to LGBT organizations. Only with funding, we can reserve space and host events. ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 6LGBT/ ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 6Connect with all possible resources. If you look hard and creative enough, you will find that there is an abundance of domestic LGBT resources. You can invite guests in the local or the greater LGBT community for in-person or remote talks. You can also ask to use other organizations' educational material or other rainbow-related products. ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 7LGBT ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 7Make good use of new media platforms. Recruit new members and publicize events through easy-to-use platforms such as Wechat official accounts or Weibo, and ask other LGBT accounts to forward. Moreover, keep up online interactions: events like personal story submission for prizes can keep followers active. ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 8//LGBT ")]),_c('p',{staticClass:"bullet"},[_vm._v(" 8Be on good terms with the admins of the school/be a heard-but-never-seen mysterious organization. If the school holds a relatively positive attitude toward diversity/gender issues, consider collaborating with existing school clubs, appling to use school property under any name, and even trying to register the group as an official student club. If the school is unfriendly, do not let the school know the member roster so as to avoid risks. Organize on-campus flash events if possible, or reach out to off-campus, LGBT-friendly space. ")]),_c('p',[_vm._v(" The majority of LGBT Student Organization Survival Guide seems applicable to almost any starting student organizations. However, as a guide written in pre-covid time, there is an overly strong reliance on online platforms, highly possibly due to the inaccessibility of physical spaces. Moreover, the last point on the guide reveals the difficulty that is specific to LGBTQ-related organizations. The attentiveness to the relationship with the school admins and the willingness to be a mysterious organization shows the unfriendliness of the public space toward the LGBTQ groups. Student LGBTQ organizations in Chinese universities often struggle to exist for they could face much discrimation and pressure from the school. In fact, many universities directly forbid the forming of LGBTQ organizations. ")]),_c('p',{staticClass:"bullet"},[_vm._v(" LGBT Student Organization Survival Guide is written by a former Rainbow Club organizer (see Rainbow Club). ")]),_vm._m(1)]):_vm._e(),(_vm.view === 5)?_c('div',{staticClass:"text"},[_c('p',{staticClass:"caption"},[_vm._v(" Calendar with May 17th marked ")]),_c('p',[_vm._v(" May 17th is , International Day Against Homophobia, Transphobia and Biphobia. It is the most celebrated day among LGBTQ communities in China. Most organized Pride events take place on this day. However, this is also the day where uncomfortableness or even resistance toward LGBTQ expressions is strongly felt. According to a student blog post, carrying a pride flag on May 17th attracted many unwelcoming looks from strangers and the student felt pressure to hide the flag. In more extreme cases, organized university pride events can face serious warnings or even shutdowns. ")]),_vm._m(2)]):_vm._e(),(_vm.view === 6)?_c('div',{staticClass:"text"},[_c('p',{staticClass:"caption"},[_vm._v(" 10 rainbow flags in Tsinghua University ")]),_c('p',[_vm._v(" Two students in Tsinghua University, one of the most prestigious universities in China, were punished for placing 10 rainbow flags on May 14th, 2022 in the message area of the supermarket in the school. The students were asked to talk immediately by the university. When refused to talk,the two students were faced with graduation threats and the possibility of being expelled. The school advisor and professor eventually broke into the students dorm rooms, and the two students were forced to talk. Both received warning disciplinary decisions in mid July 2022. An article regarding this incident that was fastly circulating on WeChat was deleted within a day of posting. ")]),_vm._m(3)]):_vm._e(),(_vm.view === 7)?_c('div',{staticClass:"text"},[_c('p',{staticClass:"caption"},[_vm._v(" Identity keychains ")]),_c('p',[_vm._v(" In China, there is a system of categorizing the sub-groups within the LGBTQ community different from that of western countries. The broad categories for homosexual man is 1, 0.5, and 0, and for homosexual women is T, H, and P. The categorization is based on existing stereotypes of romantic relationships: 1 is someone who takes the lead and performs the active role, while 0 is someone who follows the lead and performs the passive role. A 0.5 is someone in betweensometimes leading and active, and other times following and passive. For homsexual women, the categories are based on gender stereotypes. T stands for tomboy, where the person presents themself like a man. P stands for pretty girl, princess, or (p, woman), where the person dresses more girlish and presents themself like a girl. H is between T and P, where the person sometimes dresses more girly and other times more boyish, or presents themself in gender neutral ways. When describing ones identity in China, those six broad terms are most often used instead of the system of lesbian, gay, bisexual, etc. terminology. ")])]):_vm._e(),(_vm.view === 8)?_c('div',{staticClass:"text"},[_vm._m(4),_c('p',[_vm._v(" Butterfly is 2004 Hong Kong film of a lesbian love story from the award-winning director Yan Yan Mak. The film was screened by numerous prominent film festivals across the world. Although Hong Kong was strongly advertised as an integrated part of China in the decade when film was made and the newly revived Chinese film industry desperately needs globally acclaimed films such as Butterfly to prove itself internationally, Butterfly was nonetheless never allowed in theater in mainland China due to its homosexual content. ")])]):_vm._e(),(_vm.view === 9)?_c('div',{staticClass:"text"},[_vm._m(5),_c('p',[_vm._v(" Shanghai Pride is the first and biggest annual pride festival in mainland China. Starting in 2009, it hosts events such as art exhibitions, film screenings and talks in private venues every second week of June. However, in August of 2020, the organizer suddenly announced that they are shutting down ShanghaiPRIDE, canceling all future events, with no explanation. Many involved refused to be interviewed on the matter, but it is widely speculated that the shutdown is due to pressure from local authorities. ")]),_vm._m(6)]):_vm._e(),(_vm.view===10)?_c('div',{staticClass:"text"},[_vm._m(7),_c('p',[_vm._v(" Zhongshan University was the first to have a recognized club dedicated to LGBTQ topics in mainland China. Their club, Rainbow Club, was established in 2006 and organized many LGBTQ-related events. The forming of the club was covered by numerous news stories. However, Zhongshan Universitys progressiveness did not last long. Shortly after its establishment, Rainbow club started to face much suppression and erasure: for years, its existence became a myth passed among students. Now, the club had long been erased from the school directory, but disassociated from the university, this student-run organization remains active in the local community. ")])]):_vm._e(),(_vm.view===11)?_c('div',{staticClass:"text"},[_vm._m(8),_c('p',[_vm._v(" An extremely well-known phrase by the revolutionary Sun Yat-sen in 1923 proposing and fighting for a republic China against the monarchy. In the recent decades, the word (comrade) has took on another meaning: it became another term for homosexual. Almost a hundred year after it was originally spoken, this phrase takes on a different meaning too. In suppressed pride events organized by college students in Guangzhou, the phrase is shouted to show the students willingness to continue their effort to advocate for the LGBTQ community in public despite the institutional resistance they faced. ")]),_vm._m(9),_c('img',{staticClass:"center-portrait",attrs:{"src":"/images/the_closet_12.jpeg"}}),_vm._m(10),_c('p',[_vm._v(" Li Ying, a crucial player in China womens national soccer team, posted images of her and her girlfriend in June of 2021 celebrating their one-year anniversary, as the chocolate cake in the picture clearly writes Happy One-year Anniversary with her and her girlfriends initials. This post was immediately deleted upon posting. Such a claim of her LGBTQ identity is extremely unusual for national athletes. Some suspect that the team hired to help Li Ying run her social media as a public figure deleted the post, for this post and thus her LGBT identity would be considered inappropriate for a national athlete. ")]),_vm._m(11),_c('img',{staticClass:"center-portrait",attrs:{"src":"/images/the_closet_13.jpeg"}}),_vm._m(12),_c('p',[_vm._v(" Inspired from the 2018 anime TV series Citrus, a romance between two girls,  is a neutral, informal adjective describing things that have lesbian connotation. It is a new phrase among many previously existing coded languages used when referring to LGBTQ-related or LGBTQ-leaning content. ")])]):_vm._e(),_c('div',{staticClass:"footer"})])
